*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 600;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/cormorant-garamond-600.woff2") format("woff2"), url("../fonts/cormorant-garamond-600.woff") format("woff"); }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/cormorant-garamond-700.woff2") format("woff2"), url("../fonts/cormorant-garamond-700.woff") format("woff"); }

@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/cormorant-garamond-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-italic.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff"); }

@font-face {
  font-family: "Poppins";
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

.main-page {
  height: 100%;
  scroll-behavior: smooth; }

.page-body {
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.page-body .main {
  flex-grow: 1; }

.center {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px; }
  @media (max-width: 1024px) {
    .center {
      padding: 0 20px; } }

.header {
  position: relative;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  color: #1d1613; }
  @media (max-width: 1023px) {
    .header {
      background-color: #a87b62;
      color: #ffffff;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: min-content, 1fr;
      row-gap: 12px;
      padding: 30px 45px 40px; } }
  @media (max-width: 1023px) and (max-width: 767px) {
    .header {
      padding: 20px 15px 10px;
      row-gap: 16px; } }
  @media (max-width: 1023px) {
    .header--js {
      background-color: #ffffff;
      color: #1d1613;
      padding: 30px 45px 24px; } }
  @media (max-width: 767px) {
    .header--js {
      padding: 20px 15px 14px; } }
  @media (max-width: 1023px) {
    .header--js-open {
      position: fixed;
      min-height: 100%;
      max-height: 100%;
      width: 100%;
      overflow-y: scroll;
      z-index: 20; } }

.header a {
  text-decoration: none;
  color: inherit; }

.header ul {
  list-style: none; }

.header__menu-button {
  display: none; }
  @media (max-width: 1023px) {
    .header__menu-button {
      display: block;
      border: none;
      background: none;
      grid-area: 1/1/2/2;
      padding: 0;
      margin: 0 auto 0 0;
      height: 25px;
      align-self: center; } }

.header__wrapper {
  border-bottom: 1px solid #f6f5f4; }
  @media (max-width: 1023px) {
    .header__wrapper {
      border: none; } }

.header__user-menu {
  font-size: 14px;
  line-height: 21px;
  padding: 30px 20px 12px;
  display: grid;
  grid-template-columns: 1fr min-content 1fr; }
  @media (max-width: 1023px) {
    .header__user-menu {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      row-gap: 35px;
      padding: 0; } }
  @media (max-width: 767px) {
    .header__user-menu {
      row-gap: 25px; } }

@media (max-width: 1023px) {
  .header--js .header__user-menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: none; } }

.header__logo {
  margin: 0 auto;
  padding: 0 20px 0; }
  @media (max-width: 1023px) {
    .header__logo {
      grid-area: 1/2/2/3; } }

.header__logo svg {
  color: #a87b62; }
  @media (max-width: 1023px) {
    .header__logo svg {
      color: #ffffff; } }
  @media (max-width: 767px) {
    .header__logo svg {
      width: 52px;
      height: 52px; } }

@media (max-width: 1023px) {
  .header--js .header__logo svg {
    color: #a87b62; } }

.header__form-search {
  position: relative;
  display: flex;
  order: -1;
  height: 40px;
  padding: 7px 0; }
  @media (max-width: 1023px) {
    .header__form-search {
      grid-area: 2/1/3/4; } }

@media (max-width: 1023px) {
  .header--js .header__form-search {
    display: none; } }

.header__form-button {
  background: none;
  border: none;
  padding: 6px 0 0; }
  @media (max-width: 1023px) {
    .header__form-button {
      position: absolute;
      left: 10px;
      top: 5px; } }

@media (max-width: 1023px) {
  .header__form-button svg {
    width: 16px;
    height: 16px; } }

.header__search {
  border: none;
  padding: 0 0 0 15px; }
  .header__search::placeholder {
    color: #9b9997; }
    @media (max-width: 1023px) {
      .header__search::placeholder {
        opacity: 0; } }
  @media (max-width: 1023px) {
    .header__search {
      padding: 0 0 0 50px;
      height: 40px;
      width: 100%;
      background-color: rgba(255, 255, 255, 0.15);
      border-radius: 21px; } }

.header__search svg {
  margin: 0 12px 0 0; }

.header__user-menu-list {
  letter-spacing: 0.04em;
  padding: 12px 0 0;
  margin: 0 0 0 auto;
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 1023px) {
    .header__user-menu-list {
      grid-area: 1/3/2/4;
      padding: 0;
      align-self: center; } }

@media (max-width: 1023px) {
  .header__cart-desktop {
    display: none; } }

.header__cart-tablet {
  display: none; }
  @media (max-width: 1023px) {
    .header__cart-tablet {
      display: block;
      fill: #a87b62; } }

.header--js .header__cart-tablet {
  fill: #ffffff; }

.header__user-menu-list li:not(:last-child) {
  margin: 0 33px 0 0; }

@media (max-width: 1023px) {
  .header__login-desktop {
    display: none; } }

@media (max-width: 1023px) {
  .header__nav {
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500; } }

@media (max-width: 1023px) {
  .header--js .header__nav {
    display: none; } }

@media (max-width: 1023px) {
  .header__nav ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0; } }

.header__nav-list {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.01em;
  min-height: 61px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-right: 8px; }
  @media (max-width: 1023px) {
    .header__nav-list {
      margin: 0 0 40px; } }
  @media (max-width: 767px) {
    .header__nav-list {
      margin: 0 0 30px; } }

@media (max-width: 1023px) {
  .header__nav-list a {
    font-size: 24px;
    line-height: 36px; } }

.header__nav-list li:not(:last-child) {
  margin: 0 35px 0 0; }
  @media (max-width: 1023px) {
    .header__nav-list li:not(:last-child) {
      margin: 0 0 21px; } }

.header__list-tablet {
  display: none; }

@media (max-width: 1023px) {
  .header__list-tablet li {
    margin: 0 0 19px; } }

@media (max-width: 1023px) {
  .header__list-tablet li:last-child {
    margin: 23px 0 19px; } }

@media (max-width: 767px) {
  .header__list-tablet li:last-child {
    margin: 14px 0 19px; } }

.header__list-tablet a {
  font-size: 18px;
  line-height: 23px;
  color: rgba(255, 255, 255, 0.75); }

.header__login-tablet {
  display: none; }
  @media (max-width: 1023px) {
    .header__login-tablet {
      display: block;
      font-size: 18px;
      line-height: 23px;
      letter-spacing: 0.01em;
      margin: 23px 0 0; } }
  @media (max-width: 767px) {
    .header__login-tablet {
      margin: 30px 0 0; } }

.lead {
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  color: #ffffff;
  text-align: center;
  position: relative;
  background-color: #a87b62;
  background-image: url("../img/backgrounds/index.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: -8px;
  min-height: 463px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 0; }
  .lead::before {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    background-color: rgba(86, 64, 52, 0.7); }
  @media (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .lead {
      background-image: url("../img/backgrounds/index@2x.jpg"); } }
  @media (max-width: 1023px) {
    .lead {
      background-position: 50%; } }

.lead h1 {
  font-weight: 700;
  font-size: 45px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin: 0;
  z-index: 10; }

.lead p {
  font-weight: 400;
  font-style: italic;
  font-size: 38px;
  line-height: 49px;
  margin: 0;
  z-index: 10; }

.footer {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  background-color: #000000;
  color: #ffffff;
  padding: 90px 0 100px; }
  @media (max-width: 1023px) {
